import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			editType:'',
			id:'',
			name:'',
			phone:'',
			detail:'',
			isDefault:0,
			cityPickerValue: [0, 0, 1],
			themeColor: '#007AFF',
			region:{label:"请点击选择地址",value:[],cityCode:""},
			adressDetail:{},
			addressId:'',
			regionAddress:'',
			
		};
	},
	methods: {
		onCancel(e) {
			console.log(e)
		},
		chooseCity() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.region = e;
			let addressArr = e.label.split('-');
			
			this.regionAddress = addressArr[0]+','+addressArr[1]+','+addressArr[2];
			this.cityPickerValue = e.value;
		},
		//设置默认地址
		isDefaultChange(e){
			let self = this;
			self.isDefault = e.detail.value == true? 1:0;
			
			// if(self.id){ //修改地址
			// 	console.log(222,self.id);
			// 	// self.$axios(self.$baseUrl.setDefaultAdress,{
			// 	// 	id:self.id
			// 	// })
			// 	// .then(res =>{
			// 	// 	if(res.data.code == 1){
			// 	// 		uni.navigateBack();
			// 	// 	}
			// 	// 	
			// 	// },(error) =>{
			// 	// 	console.log(error)
			// 	// })
			// 	
			// }else{//新增地址
			// 	self.isDefault = e.detail.value == true? 1:0;
			// 	// uni.showToast({title:'设置成功',icon:'success'});
			// }
			
		},
		del(){
			let self = this;
			uni.showModal({
				title: '删除提示',
				content: '你将删除这个收货地址',
				success: (res)=>{
					if (res.confirm) {
						self.$axios(self.$baseUrl.delAdress,{
							id:self.id
						})
						.then(res =>{
							if(res.data.code == 1){
								uni.navigateBack();
							}
							
						},(error) =>{
							console.log(error)
						})
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			
		},
		save(){
			let self = this;
			/* "head":this.name.substr(0,1), */
			// "address":{"region":this.region,"detailed":this.detailed},
			console.log(11111111111,self.regionAddress)
			
			if(!self.name){
				uni.showToast({title:'请输入收件人姓名',icon:'none'});
				return ;
			}
			if(!self.phone){
				uni.showToast({title:'请输入收件人电话号码',icon:'none'});
				return ;
			}else{
				if(!(/^1[345678]\d{9}$/.test(self.phone))){ 
					uni.showToast({title:'请输入正确的电话号码',icon:'none'});
					return false; 
				} 
			}
			if(!self.detail){
				uni.showToast({title:'请输入收件人详细地址',icon:'none'});
				return ;
			}
			if(!self.regionAddress){
				uni.showToast({title:'请选择收件地址',icon:'none'});
				return ;
			}
			uni.showLoading({
				title:'正在提交'
			})
			//实际应用中请提交ajax,模板定时器模拟提交效果  addAdress
			if(self.id){//修改  editAdress
				self.$axios(self.$baseUrl.editAdress,{
					id:self.id,
					name:self.name,
					phone:self.phone,
					detail:self.detail,
					isdefault:self.isDefault,
					region:self.regionAddress
				})
				.then(res =>{
				
					if(res.data.code == 1){
						uni.showToast({title:'保存成功！',icon:'success'});
						uni.hideLoading();
						uni.navigateBack(Object);
					}
					
				},(error) =>{
					console.log(error)
				})
				
			}else{//新增
				self.$axios(self.$baseUrl.addAdress,{
					name:self.name,
					phone:self.phone,
					detail:self.detail,
					isdefault:self.isDefault,
					region:self.regionAddress
				})
				.then(res =>{
				
					if(res.data.code == 1){
						uni.showToast({title:'保存成功！',icon:'success'});
						uni.hideLoading();
						uni.navigateBack(Object);
					}
					
				},(error) =>{
					console.log(error)
				})
			}
			
			
		}
	},
	async onLoad(e) {
		let self = this;
		if(e.type=='edit'){
			//新增地址
			self.save();
		}else{
			//修改地址
			self.editType = 'edit';
			//获取传递过来的参数
			let detailId = e.id;
			self.$axios(self.$baseUrl.getAdressDetail,{
				id :detailId
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					self.addressId = result.detail.address_id;
					self.name = result.detail.name;
					self.phone = result.detail.phone;
					self.detail = result.detail.detail;
					self.region.label = result.detail.Area.province +result.detail.Area.city +result.detail.Area.region ;
					self.id = result.detail.address_id;
					self.isDefault = result.detail.isdefault == '1'?true:false;
					this.regionAddress = result.detail.Area.province+','+result.detail.Area.city+','+result.detail.Area.region;
				}
				
			},(error) =>{
				console.log(error)
			})
		}
		
	},
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
};
